video {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
}

.hero-container4{
    padding-top: 10rem;
    height: 60vh;
    width: 100%;
  background: #212529;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-size: cover;
  flex-direction: column;
    align-items: center;
    /*box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.2);*/
    object-fit: contain;
    display: flex;
}

.hero-container4 > h1 {
    text-align: center;
    vertical-align: middle;
    color: black;
    font-size: 70px;
    margin-top: 100px;
    z-index: 1;
}

.hero-container4 > p{
    text-align: center;
    vertical-align: middle;
    margin-top: 8px;
    color: black;
    font-size: 32px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    z-index: 10;
}


.hero-btns4 {
    color: black;
    margin-top: 32px;
}

.hero-btns3 .btn {
    margin: 6px;
}

.fa-play-circle {
    margin-left: 4px;
}

@media screen and (max-width: 960px) {
    .hero-container4 > h1 {
        font-size: 60px;
        margin-top: 150px;
    }
}

@media screen and (max-width: 768px) {
    .hero-container4 > h1 {
        font-size: 50px;
        margin-top: 100px;
    }
    
    .hero-container4 > p {
        font-size: 25px;   
    }

    .btn-mobile{
        display: block;
        text-decoration: none;
    }

    .btn {
        width: 100%;
    }
}